.section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1215px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 50px;
}
.img {
  margin-right: 25px;
}
.block {
  display: flex;
  align-items: center;
}
.logo {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
}

.list {
  display: flex;
  align-items: center;
  gap: 37px;
}

.item {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #202020;
}
